import { useState, useEffect, useRef, useCallback } from "react";
import { toast } from "react-hot-toast";
import { copyToClipboard } from "../utils/clipboard";
import { useOrchestration } from "./useOrchestration";
import { useWebSocket } from "./useWebSocket";
import { crewAPI, handleAPIError } from "../services/api";
import { useAuth } from "../contexts/AuthContext";

/**
 * - AI   UI    
 */
const processMultiAgentResponse = (result) => {
  if (!result) {
    console.error("-  :  ", result);
    return {
      id: "error-" + Date.now(),
      type: "assistant",
      content: "   .  .",
      timestamp: new Date().toISOString(),
      isError: true,
    };
  }

  // -  
  const agentResults = result.agentResults || {};
  const finalTitles = result.finalTitles || {};
  const tokenUsage = result.tokenUsage || 0;

  //   
  const agentSummary = Object.keys(agentResults).map(agentType => {
    const agentResult = agentResults[agentType];
    const titles = finalTitles[agentType] || [];
    
    return {
      agentType,
      agentName: getAgentName(agentType),
      result: agentResult?.content || " ",
      titles: titles,
      tokenCount: agentResult?.tokenCount || 0
    };
  });

  //    
  const allTitles = Object.values(finalTitles).flat();

  return {
    id: "multi-agent-" + Date.now(),
    type: "assistant",
    content: formatMultiAgentContent(agentSummary, allTitles),
    timestamp: new Date(),
    // -  
    isMultiAgent: true,
    agentResults: agentSummary,
    allTitles: allTitles,
    tokenUsage: tokenUsage,
  };
};

/**
 *     
 */
const getAgentName = (agentType) => {
  const agentNames = {
    journalism: "  ",
    balanced: "  ", 
    click: " ",
    seo: " SEO/AEO ",
    social: "  "
  };
  return agentNames[agentType] || ` ${agentType}`;
};

/**
 * -  UI   
 */
const formatMultiAgentContent = (agentSummary, allTitles) => {
  let content = " **-  **\n\n";
  
  //   
  if (allTitles.length > 0) {
    content += " **  :**\n";
    allTitles.forEach((title, index) => {
      content += `${index + 1}. ${title}\n`;
    });
    content += "\n";
  }

  //   
  content += " **  :**\n\n";
  agentSummary.forEach(agent => {
    content += `### ${agent.agentName}\n`;
    if (agent.titles.length > 0) {
      content += "** :**\n";
      agent.titles.forEach(title => {
        content += ` ${title}\n`;
      });
    }
    content += `** :** ${agent.tokenCount}\n\n`;
  });

  return content;
};

/**
 *     
 * @param {string} projectId -  ID
 * @param {string} projectName -  
 * @param {Array} promptCards -   
 * @returns {Object} -    
 */
export const useChat = (projectId, projectName, promptCards = [], conversationId = null) => {
  const { user } = useAuth(); // Add user from AuthContext
  
  //   (  )
  const isFirstRender = useRef(true);
  if (isFirstRender.current) {
    console.log('useChat :', {
      projectId,
      projectName,
      promptCardsLength: promptCards?.length,
      conversationId,
      userId: user?.id
    });
    isFirstRender.current = false;
  }
  const [messages, setMessages] = useState([]);
  const [inputValue, setInputValue] = useState("");
  const [copiedMessage, setCopiedMessage] = useState(null);
  const [canSendMessage, setCanSendMessage] = useState(true);
  const [inputHeight, setInputHeight] = useState(24); //   
  const [selectedModel, setSelectedModel] = useState("anthropic.claude-3-5-sonnet-20241022-v2:0");
  const streamingMessageIdRef = useRef(null);
  const currentWebSocketRef = useRef(null);
  const currentExecutionIdRef = useRef(null);
  

  const messagesEndRef = useRef(null);
  const inputRef = useRef(null);

  //    
  const [isUserScrolling, setIsUserScrolling] = useState(false);
  const scrollContainerRef = useRef(null);
  const lastScrollTopRef = useRef(0);

  const {
    isExecuting: isGenerating,
    isStreaming,
    executeOrchestration,
    resetOrchestration,
  } = useOrchestration(projectId);

  // WebSocket  
  const {
    isConnected: wsConnected,
    isConnecting: wsConnecting,
    error: wsError,
    startStreaming: wsStartStreaming,
    addMessageListener,
    removeMessageListener,
  } = useWebSocket(projectId);

  //    - projectId  
  useEffect(() => {
    setMessages([]); //   
  }, [projectId]); // projectName  projectId 

  //    
  const handleScroll = useCallback(() => {
    if (!scrollContainerRef.current) return;

    const container = scrollContainerRef.current;
    const currentScrollTop = container.scrollTop;
    const maxScrollTop = container.scrollHeight - container.clientHeight;

    //    
    if (Math.abs(currentScrollTop - lastScrollTopRef.current) > 2) {
      const isAtBottom = currentScrollTop >= maxScrollTop - 20;

      //      ,     
      setIsUserScrolling(!isAtBottom);
    }

    lastScrollTopRef.current = currentScrollTop;
  }, []);

  const scrollToBottom = useCallback(() => {
    //       
    if (!isUserScrolling && messagesEndRef.current) {
      messagesEndRef.current.scrollIntoView({ behavior: "smooth" });
    }
  }, [isUserScrolling]);

  //      (    )
  useEffect(() => {
    scrollToBottom();
  }, [messages, scrollToBottom]);

  // WebSocket   
  useEffect(() => {
    const handleWebSocketMessage = (event) => {
      try {
        const data = JSON.parse(event.data);
        console.log("WebSocket  :", data);

        const currentStreamingId = streamingMessageIdRef.current;

        switch (data.type) {
          case "stream_start":
            console.log("WebSocket  ");
            break;

          case "progress":
            //     UI  
            console.log(` : ${data.step} (${data.progress}%)`);
            break;

          case "stream_chunk":
            if (currentStreamingId) {
              setMessages((prev) => {
                const updatedMessages = [...prev];
                const streamingMsgIndex = updatedMessages.findIndex(
                  (msg) => msg.id === currentStreamingId
                );

                console.log("  :", {
                  currentStreamingId,
                  streamingMsgIndex,
                  messagesLength: prev.length,
                  content: data.content
                });

                if (streamingMsgIndex !== -1) {
                  //     
                  const currentContent =
                    updatedMessages[streamingMsgIndex].content || "";

                  updatedMessages[streamingMsgIndex] = {
                    ...updatedMessages[streamingMsgIndex],
                    content: currentContent + data.content,
                    isLoading: true,
                    isStreaming: true,
                  };
                  
                  console.log(" :", updatedMessages[streamingMsgIndex]);
                } else {
                  console.log("    ,  ID:", prev.map(m => m.id));
                }

                return updatedMessages;
              });
              //         
              if (!isUserScrolling) {
                scrollToBottom();
              }
            } else {
              console.log("currentStreamingId null");
            }
            break;

          case "stream_complete":
            if (currentStreamingId) {
              setMessages((prev) => {
                const updatedMessages = [...prev];
                const streamingMsgIndex = updatedMessages.findIndex(
                  (msg) => msg.id === currentStreamingId
                );

                if (streamingMsgIndex !== -1) {
                  updatedMessages[streamingMsgIndex] = {
                    ...updatedMessages[streamingMsgIndex],
                    content: data.fullContent,
                    isLoading: false,
                    isStreaming: false,
                    timestamp: new Date().toISOString(),
                  };
                }

                return updatedMessages;
              });
              streamingMessageIdRef.current = null;
              scrollToBottom();
            }
            break;

          case "error":
            console.error("WebSocket  :", data.message);
            if (currentStreamingId) {
              setMessages((prev) => {
                const updatedMessages = [...prev];
                const streamingMsgIndex = updatedMessages.findIndex(
                  (msg) => msg.id === currentStreamingId
                );

                if (streamingMsgIndex !== -1) {
                  //      
                  let errorContent = "    .  .";
                  
                  if (data.message?.includes("401") || data.message?.includes("Unauthorized")) {
                    errorContent = " .  .";
                  } else if (data.message?.includes("timeout") || data.message?.includes(" ")) {
                    errorContent = "  .      .";
                  } else if (data.message?.includes("rate limit") || data.message?.includes("")) {
                    errorContent = "  .    .";
                  }

                  updatedMessages[streamingMsgIndex] = {
                    ...updatedMessages[streamingMsgIndex],
                    content: errorContent,
                    isLoading: false,
                    isStreaming: false,
                    isError: true,
                    timestamp: new Date().toISOString(),
                  };
                }

                return updatedMessages;
              });
              streamingMessageIdRef.current = null;
            }
            
            //    
            const toastMessage = data.message?.includes("401") 
              ? " " 
              : "   ";
            toast.error(toastMessage);
            break;

          default:
            console.log("   WebSocket  :", data.type);
        }
      } catch (error) {
        console.error("WebSocket   :", error);
      }
    };

    if (wsConnected) {
      addMessageListener(handleWebSocketMessage);
    }

    return () => {
      if (wsConnected) {
        removeMessageListener(handleWebSocketMessage);
      }
    };
  }, [wsConnected, addMessageListener, removeMessageListener, scrollToBottom]);

  /**
   *    
   */
  const handleStreamingResponse = useCallback(
    (chunk, metadata) => {
      const currentStreamingId = streamingMessageIdRef.current;

      console.log(" :", chunk, " ID:", currentStreamingId);

      if (!currentStreamingId) {
        console.error(" ID !");
        return;
      }

      setMessages((prev) => {
        const updatedMessages = [...prev];
        const streamingMsgIndex = updatedMessages.findIndex(
          (msg) => msg.id === currentStreamingId
        );

        if (streamingMsgIndex !== -1) {
          //    
          updatedMessages[streamingMsgIndex] = {
            ...updatedMessages[streamingMsgIndex],
            content: updatedMessages[streamingMsgIndex].content + chunk,
            isLoading: true,
            isStreaming: true,
          };
          console.log(
            "   :",
            updatedMessages[streamingMsgIndex].content
          );
        } else {
          console.error("    :", currentStreamingId);
        }

        return updatedMessages;
      });

      //   (    )
      if (!isUserScrolling) {
        scrollToBottom();
      }
    },
    [scrollToBottom, isUserScrolling]
  );

  /**
   *    
   */
  const handleStreamingComplete = useCallback(
    (result) => {
      const currentStreamingId = streamingMessageIdRef.current;

      console.log(" :", result, " ID:", currentStreamingId);

      if (!currentStreamingId) {
        console.error("    ID !");
        return;
      }

      setMessages((prev) => {
        const updatedMessages = [...prev];
        const streamingMsgIndex = updatedMessages.findIndex(
          (msg) => msg.id === currentStreamingId
        );

        if (streamingMsgIndex !== -1) {
          //    
          updatedMessages[streamingMsgIndex] = {
            ...updatedMessages[streamingMsgIndex],
            content: result.result,
            isLoading: false,
            isStreaming: false,
            performance_metrics: result.performance_metrics,
            model_info: result.model_info,
            timestamp: new Date().toISOString(),
          };
          console.log(
            "   :",
            updatedMessages[streamingMsgIndex].content
          );
        } else {
          console.error(
            "       :",
            currentStreamingId
          );
        }

        return updatedMessages;
      });

      //  ID 
      streamingMessageIdRef.current = null;

      //  
      console.log("WebSocket   -  ");
      setCanSendMessage(true);

      //   (    )
      scrollToBottom();
    },
    [scrollToBottom]
  );

  /**
   *   
   */
  const handleStopGeneration = useCallback(() => {
    console.log("  ");

    // WebSocket  
    if (currentWebSocketRef.current) {
      currentWebSocketRef.current.close();
      currentWebSocketRef.current = null;
    }

    //     
    if (currentExecutionIdRef.current) {
      //   API      
      currentExecutionIdRef.current = null;
    }

    //    
    const currentStreamingId = streamingMessageIdRef.current;
    if (currentStreamingId) {
      setMessages((prev) => {
        const updatedMessages = [...prev];
        const streamingMsgIndex = updatedMessages.findIndex(
          (msg) => msg.id === currentStreamingId
        );

        if (streamingMsgIndex !== -1) {
          updatedMessages[streamingMsgIndex] = {
            ...updatedMessages[streamingMsgIndex],
            content:
              updatedMessages[streamingMsgIndex].content +
              "\n\n[ ]",
            isLoading: false,
            isStreaming: false,
            timestamp: new Date().toISOString(),
          };
        }

        return updatedMessages;
      });

      streamingMessageIdRef.current = null;
    }

    //    
    setCanSendMessage(true);

    // orchestration  
    resetOrchestration();

    toast.success(" ");
  }, [resetOrchestration]);

  /**
   *    
   */
  const adjustInputHeight = useCallback((value) => {
    if (!value.trim()) {
      setInputHeight(24); //  
      return;
    }
    
    //    ()
    const lines = value.split('\n').length;
    const charBasedLines = Math.ceil(value.length / 80); // 80 1 
    const estimatedLines = Math.max(lines, charBasedLines);
    
    //   (lineHeight: 1.4, fontSize: 16px)
    let calculatedHeight;
    if (estimatedLines <= 3) {
      calculatedHeight = 24 + (estimatedLines - 1) * 22; //  +  
    } else if (estimatedLines <= 10) {
      calculatedHeight = 150 + (estimatedLines - 6) * 15; //  
    } else {
      calculatedHeight = Math.min(400, 150 + (estimatedLines - 6) * 12); //  400px
    }
    
    setInputHeight(Math.max(24, calculatedHeight));
  }, []);
  
  /**
   *   
   */
  const handleInputChange = useCallback((value) => {
    setInputValue(value);
    adjustInputHeight(value);
  }, [adjustInputHeight]);

  /**
   *  
   */
  const handleSendMessage = useCallback(async () => {
    console.log("  :", {
      inputValue: inputValue.trim(),
      isGenerating,
      canSendMessage,
    });

    if (!inputValue.trim() || isGenerating) {
      console.log(" :  ");
      return;
    }

    //  
    console.log(" ");
    setCanSendMessage(false);

    const userMessage = {
      id: "user-" + Date.now(),
      type: "user",
      content: inputValue.trim(),
      timestamp: new Date().toISOString(),
    };

    //   ID 
    const streamMsgId = "streaming-" + Date.now();
    streamingMessageIdRef.current = streamMsgId;

    console.log("   ID :", streamMsgId);

    //     
    const streamingMessage = {
      id: streamMsgId,
      type: "assistant",
      content: "",
      timestamp: new Date().toISOString(),
      isLoading: true,
      isStreaming: true,
    };

    setMessages((prev) => [...prev, userMessage, streamingMessage]);
    setInputValue("");
    setInputHeight(24); //   

    //   +       
    const allMessages = [...messages, userMessage];
    const chatHistory = allMessages
      .filter((msg) => !msg.isLoading && !msg.isError && !msg.isStreaming)
      .map((msg) => ({
        role: msg.type === "user" ? "user" : "assistant",
        content: msg.content,
      }));

    //     ( 50    )
    const maxHistoryLength = 50;
    const trimmedChatHistory = chatHistory.slice(-maxHistoryLength);

    console.log("  :", {
      totalMessages: allMessages.length,
      fullHistoryLength: chatHistory.length,
      trimmedHistoryLength: trimmedChatHistory.length,
      maxHistoryLength: maxHistoryLength,
      recentHistory: trimmedChatHistory.slice(-6), //  6  
    });

    try {
      //     -       
      const safePromptCards = Array.isArray(promptCards) ? promptCards : [];
      const activePromptCards = safePromptCards
        .filter((card) => card.isActive !== false && card.enabled !== false)
        .map((card) => ({
          promptId: card.promptId || card.prompt_id,
          title: card.title || "Untitled",
          prompt_text: card.prompt_text || card.content || "",
          tags: card.tags || [],
          isActive: card.isActive !== false,
          stepOrder: card.stepOrder || 0,
        }))
        .filter((card) => card.prompt_text.trim()) //    
        .sort((a, b) => (a.stepOrder || 0) - (b.stepOrder || 0)); // stepOrder 

      console.log("   :", {
        messageContent: userMessage.content,
        chatHistoryLength: trimmedChatHistory.length,
        promptCardsCount: activePromptCards.length,
        chatHistory: trimmedChatHistory,
        promptCards: activePromptCards.map((card) => ({
          id: card.promptId,
          title: card.title,
          contentLength: card.prompt_text.length,
          stepOrder: card.stepOrder,
          hasContent: !!card.prompt_text.trim(),
        })),
      });

      // WebSocket      
      if (wsConnected) {
        console.log("WebSocket    ");
        console.log("  :", {
          projectId,
          userInput: userMessage.content,
          conversationId,
          userSub: user?.id,
          historyLength: trimmedChatHistory.length,
          promptCardsLength: activePromptCards.length
        });

        const success = wsStartStreaming(
          userMessage.content,
          trimmedChatHistory,
          activePromptCards,
          selectedModel,
          conversationId, // Add conversationId
          user?.id // Add userSub from AuthContext
        );

        if (success) {
          // WebSocket  ,   
          return;
        } else {
          console.log("WebSocket  , SSE   ");
        }
      } else {
        console.log("WebSocket , SSE  ");
      }

      //  -  
      console.log("-   ");

      // 1.    crew  
      if (activePromptCards.length > 0) {
        try {
          console.log("      ");
          await crewAPI.createCrewInstance(projectId, activePromptCards);
          console.log("   ");
        } catch (instanceError) {
          console.log("   :", instanceError.message);
          
          // 401     
          if (instanceError.response?.status === 401) {
            const { shouldRedirect } = await handleAPIError(instanceError);
            if (shouldRedirect) {
              return;
            }
          }
          
          //       (   )
          console.log("     ");
        }
      }

      // 2. -  
      const multiAgentResult = await crewAPI.executeMultiAgent(
        projectId, 
        userMessage.content,
        (progress) => {
          //   ()
          console.log("- :", progress);
        }
      );

      console.log("-  :", multiAgentResult);

      // 3.  UI  
      const assistantMessage = processMultiAgentResponse(multiAgentResult);
      
      // 4.     
      setMessages((prev) => {
        const updatedMessages = [...prev];
        const streamingMsgIndex = updatedMessages.findIndex(
          (msg) => msg.id === streamingMessageIdRef.current
        );

        if (streamingMsgIndex !== -1) {
          updatedMessages[streamingMsgIndex] = assistantMessage;
        } else {
          updatedMessages.push(assistantMessage);
        }

        return updatedMessages;
      });

      streamingMessageIdRef.current = null;
      setCanSendMessage(true);
      
      //  
      toast.success(`${Object.keys(multiAgentResult.agentResults || {}).length}   !`);
    } catch (error) {
      console.error("  :", error);

      // API   
      const { userMessage: errorUserMessage, shouldRedirect } = await handleAPIError(error);

      //     
      if (shouldRedirect) {
        return;
      }

      const errorMessage = {
        id: "error-" + Date.now(),
        type: "assistant",
        content: errorUserMessage,
        timestamp: new Date().toISOString(),
        isError: true,
        errorDetails: {
          message: error.message,
          status: error.response?.status,
          code: error.code,
        },
      };

      setMessages((prev) => {
        //    
        const currentStreamingId = streamingMessageIdRef.current;
        const filteredMessages = prev.filter(
          (msg) => msg.id !== currentStreamingId
        );
        return [...filteredMessages, errorMessage];
      });

      streamingMessageIdRef.current = null;

      //     
      setCanSendMessage(true);
    }

    //        ()
    setCanSendMessage(true);
  }, [
    inputValue,
    isGenerating,
    executeOrchestration,
    handleStreamingResponse,
    handleStreamingComplete,
    messages,
  ]);

  /**
   * Enter  
   */
  const handleKeyPress = useCallback(
    (e) => {
      if (e.key === "Enter" && !e.shiftKey) {
        e.preventDefault();
        handleSendMessage();
      }
    },
    [handleSendMessage]
  );

  /**
   *  
   */
  const handleCopyMessage = useCallback(async (content, messageId) => {
    const success = await copyToClipboard(content);
    if (success) {
      setCopiedMessage(messageId);
      setTimeout(() => setCopiedMessage(null), 2000);
    }
  }, []);

  /**
   *   
   */
  const handleCopyTitle = useCallback(async (title, messageId, index) => {
    const success = await copyToClipboard(title, "!");
    if (success) {
      setCopiedMessage(`${messageId}_title_${index}`);
      setTimeout(() => setCopiedMessage(null), 2000);
    }
  }, []);

  /**
   *  
   */
  const resetChat = useCallback(() => {
    setMessages([]);
    setInputValue("");
    setCopiedMessage(null);
    setCanSendMessage(true);
    streamingMessageIdRef.current = null;
    currentWebSocketRef.current = null;
    currentExecutionIdRef.current = null;
    resetOrchestration();
  }, [resetOrchestration]);

  return {
    messages,
    inputValue,
    setInputValue,
    handleInputChange, //   
    copiedMessage,
    isGenerating,
    isStreaming,
    canSendMessage,
    streamingMessageId: streamingMessageIdRef.current,
    messagesEndRef,
    inputRef,
    inputHeight, //  
    handleSendMessage,
    handleStopGeneration,
    handleKeyPress,
    handleCopyMessage,
    handleCopyTitle,
    resetChat,
    scrollToBottom,
    // WebSocket  
    wsConnected,
    wsConnecting,
    wsError,
    //   
    scrollContainerRef,
    handleScroll,
    isUserScrolling,
    //    
    selectedModel,
    setSelectedModel,
  };
};
