import { useState, useEffect, useCallback } from 'react';
import { conversationAPI, mockConversations } from '../services/api';

/**
 *      
 *     
 */
export const useConversations = () => {
  const [conversations, setConversations] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [hasMore, setHasMore] = useState(true);
  const [nextCursor, setNextCursor] = useState(null);

  //    
  const loadConversations = useCallback(async (reset = false) => {
    if (loading) return;
    
    setLoading(true);
    setError(null);

    try {
      const cursor = reset ? null : nextCursor;
      const response = await conversationAPI.getConversations(cursor);
      
      if (reset) {
        setConversations(response.conversations);
      } else {
        setConversations(prev => [...prev, ...response.conversations]);
      }
      
      setHasMore(response.hasMore);
      setNextCursor(response.nextCursor);
      
    } catch (err) {
      console.error('   :', err);
      console.error('Error details:', {
        message: err.message,
        status: err.response?.status,
        data: err.response?.data,
        config: err.config
      });
      
      //    
      let errorMessage = '   .';
      if (err.code === 'ERR_NETWORK') {
        errorMessage = '  .';
      } else if (err.response?.status === 403) {
        errorMessage = '  .';
      } else if (err.response?.status >= 500) {
        errorMessage = '  .    .';
      }
      
      setError(errorMessage);
      
      // API  mock  fallback
      if (reset) {
        console.log('API , mock  ');
        setConversations(mockConversations);
        setHasMore(false);
        setNextCursor(null);
      }
    } finally {
      setLoading(false);
    }
  }, [loading, nextCursor]);

  //   
  const createConversation = useCallback(async (title) => {
    try {
      const response = await conversationAPI.createConversation(title);
      const newConversation = {
        id: response.conversationId,
        title: response.title,
        startedAt: response.startedAt,
        lastActivityAt: response.lastActivityAt,
        tokenSum: 0
      };
      
      //      
      setConversations(prev => [newConversation, ...prev]);
      
      return newConversation;
    } catch (err) {
      console.error('  :', err);
      
      // API  mock  fallback
      const mockConversation = {
        id: Date.now().toString(),
        title: title || 'New Conversation',
        startedAt: new Date().toISOString(),
        lastActivityAt: new Date().toISOString(),
        tokenSum: 0
      };
      
      setConversations(prev => [mockConversation, ...prev]);
      return mockConversation;
    }
  }, []);

  //   (  ,  )
  const updateConversation = useCallback((conversationId, updates) => {
    setConversations(prev => 
      prev.map(conv => 
        conv.id === conversationId 
          ? { ...conv, ...updates, lastActivityAt: new Date().toISOString() }
          : conv
      )
    );
  }, []);

  //  
  const deleteConversation = useCallback(async (conversationId) => {
    try {
      // API    
      await conversationAPI.deleteConversation(conversationId);
      console.log('API  :', conversationId);
    } catch (err) {
      console.warn('API  ,  :', err);
      // API     
    }
    
    // API /  UI 
    setConversations(prev => prev.filter(conv => conv.id !== conversationId));
    
    return true;
  }, []);

  //   (UI ,    )
  const removeConversation = useCallback((conversationId) => {
    setConversations(prev => prev.filter(conv => conv.id !== conversationId));
  }, []);

  //    ( )
  const loadMore = useCallback(() => {
    if (hasMore && !loading) {
      loadConversations(false);
    }
  }, [hasMore, loading, loadConversations]);

  // 
  const refresh = useCallback(() => {
    loadConversations(true);
  }, [loadConversations]);

  //  
  useEffect(() => {
    loadConversations(true);
  }, []);

  return {
    conversations,
    loading,
    error,
    hasMore,
    loadMore,
    refresh,
    createConversation,
    updateConversation,
    deleteConversation,
    removeConversation
  };
};