import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { toast } from "react-hot-toast";
import { projectAPI, handleAPIError } from "../services/api";
import AdminView from "./AdminView";
import UserView from "./UserView";
import { useAuth } from "../contexts/AuthContext";
import { ChatInterfaceSkeleton } from "./skeleton/SkeletonComponents";

const ProjectDetail = () => {
  const { projectId } = useParams();
  const navigate = useNavigate();
  const { user } = useAuth();
  const [project, setProject] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    loadProjectData();
  }, [projectId, navigate]);

  const loadProjectData = async () => {
    try {
      setLoading(true);

      //        
      let projectData;
      if (user?.role === "user") {
        //     
        if (projectId && projectId !== "default") {
          try {
            projectData = await projectAPI.getProject(projectId);
          } catch (error) {
            console.log("API  ,   :", error);
            projectData = {
              projectId: "chat",
              name: "AI ",
              description: "AI  ",
              promptCards: [],
              createdAt: new Date().toISOString(),
              updatedAt: new Date().toISOString(),
            };
          }
        } else {
          //    
          projectData = {
            projectId: "chat",
            name: "AI ",
            description: "AI  ",
            promptCards: [],
            createdAt: new Date().toISOString(),
            updatedAt: new Date().toISOString(),
          };
        }
      } else {
        //  
        try {
          projectData = await projectAPI.getProject(projectId);
        } catch (error) {
          console.error("   :", error);
          throw error; //    
        }
      }

      setProject(projectData);
    } catch (error) {
      console.error("  :", error);
      const errorInfo = handleAPIError(error);
      toast.error(errorInfo.message);

      //   
      if (user?.role === "admin") {
        navigate("/projects");
      } else {
        navigate("/dashboard");
      }
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return <ChatInterfaceSkeleton />;
  }

  if (!project) {
    return (
      <div className="min-h-screen bg-gray-50 dark:bg-gray-900 flex items-center justify-center transition-colors duration-200">
        <div className="text-center">
          <h2 className="text-xl font-semibold text-gray-900 dark:text-white mb-4">
               
          </h2>
          <button
            onClick={() => {
              if (user?.role === "admin") {
                navigate("/projects");
              } else {
                navigate("/dashboard");
              }
            }}
            className="inline-flex items-center px-4 py-2 text-sm font-medium rounded-md text-blue-700 dark:text-blue-300 bg-blue-100 dark:bg-blue-900 hover:bg-blue-200 dark:hover:bg-blue-800 focus:outline-none transition-colors duration-200"
          >
            {user?.role === "admin" ? " " : ""}
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen w-full bg-gray-50 dark:bg-dark-primary transition-all duration-500">
      {user?.role === "admin" ? (
        //  :    + 
        <AdminView
          projectId={project.projectId || projectId}
          projectName={project.name}
        />
      ) : (
        //  : 
        <UserView
          projectId={project.projectId || projectId}
          projectName={project.name}
        />
      )}
    </div>
  );
};

export default ProjectDetail;
